}

.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  fill: currentColor;
  vertical-align: middle;
  position: relative;
  transition: fill 0.15s ease-in-out;
}

.icon_pass-hidden {
  height: 16px;
  width: 16px;
}

.icon_pass-visible {
  height: 16px;
  width: 16px;
}

html {
  height: 100%;
  box-sizing: border-box;
  overflow-y: scroll;
  font-size: 16px;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  min-height: 100%;
  color: #000;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
  background-color: #eee;
}

.main {
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  background-color: #fff;
}

.bold {
  font-weight: bold;
}

.italic {
  font-style: italic;
}

.underline {
  text-decoration: underline;
}

.strike,
del {
  text-decoration: line-through;
}

.xx-small {
  font-size: 8px !important;
}

.x-small {
  font-size: 11px !important;
}

.small {
  font-size: 12px !important;
}

.large {
  font-size: 16px !important;
}

.x-large {
  font-size: 20px !important;
}

.xx-large {
  font-size: 22px !important;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

:focus,
a:focus {
  outline: none;
}

textarea {
  resize: none;
}

h1 {
  font: normal 40px/50px "Roboto", Helvetica, Arial, sans-serif;
  margin: 0 0 21px;
}

h2 {
  font: normal 20px/24px "Roboto", Helvetica, Arial, sans-serif;
  margin: 0 0 16px;
}

h3 {
  font: normal 18px/24px "Roboto", Helvetica, Arial, sans-serif;
  margin: 0 0 12px;
}

h4 {
  font: normal 16px/22px "Roboto", Helvetica, Arial, sans-serif;
  margin: 0 0 14px;
}

h5 {
  margin: 0 0 10px;
  font: bold 14px/20px "Roboto", Helvetica, Arial, sans-serif;
}

h6 {
  font-size: 16px;
  font-weight: bold;
  margin: 10px 0 8px;
}

p {
  font-size: 14px;
  line-height: 20px;
  margin: 0 0 20px;
}

p:last-child {
  margin-bottom: 0;
}

.button {
  border: none;
  padding: 0;
  background-color: #f7ca18;
  cursor: pointer;
  height: 48px;
  line-height: 48px;
  border-radius: 2px;
  width: 100%;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.75px;
  color: #2a2f34;
  transition: all 0.15s ease-in-out;
}

.button:hover {
  background-color: #d5ab07;
}

.button_icon {
  width: 16px;
  height: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #a6afb7;
  background-color: transparent;
  transition: color 0.15s ease-in-out;
  position: relative;
}

.button_icon:hover {
  background-color: transparent;
  color: #7c8995;
}

.button_icon .icon {
  pointer-events: none;
}

.button__checkbox {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

.button__checkbox:checked ~ .icon_pass-visible {
  display: inherit;
}

.button__checkbox:checked ~ .icon_pass-hidden {
  display: none;
}

.form-auth {
  background-color: #000;
  padding: 36px 0 29px;
  margin-bottom: 24px;
}

.form-auth__wrapper {
  display: flex;
  flex-direction: column;
  border: none;
  padding: 0;
  width: 68.268%;
  margin-left: auto;
  margin-right: auto;
}

.form-row {
  flex-basis: 100%;
  position: relative;
  margin-bottom: 32px;
}

.form-row:last-child {
  margin-bottom: 0;
}

.form-row .button_icon {
  position: absolute;
  top: 50%;
  right: 12px;
  transform: translateY(-50%);
}

.form-row .icon_pass-visible {
  display: none;
}

.form-row__input {
  width: 100%;
  height: 48px;
  line-height: 16px;
  color: #2a2f34;
  font-size: 14px;
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  background-color: #ebedef;
  padding: 12px 12px 0;
  border-radius: 2px 2px 0 0;
  border: none;
  border-bottom: 1px solid #6d7a87;
  box-shadow: 0 1px 0 0 transparent;
  transition: border-bottom-color 0.15s ease-in-out;
}

.form-row__input:hover {
  border-bottom-color: #2a2f34;
}

.form-row__input:active,
.form-row__input:focus {
  border-bottom-color: #f7ca18;
  box-shadow: inset 0 -1px 0 0 #f7ca18;
}

.form-row__input:focus + .form-row__label,
.form-row__input:not(:placeholder-shown) + .form-row__label {
  font-size: 11px;
  top: 6px;
  transform: none;
}

.form-row__input:focus ~ .form-row__hint,
.form-row__input:not(:placeholder-shown) ~ .form-row__hint {
  opacity: 1;
}

.form-row__input::placeholder {
  color: transparent;
}

.form-row__input::-webkit-credentials-auto-fill-button {
  visibility: hidden;
  pointer-events: none;
  position: absolute;
  right: 0;
}

.form-row__label {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 12px;
  font-size: 14px;
  color: #6d7a87;
  transition: all 0.15s ease-in-out;
  pointer-events: none;
}

.form-row__hint {
  position: absolute;
  top: 100%;
  left: 12px;
  margin-top: 5px;
  font-size: 11px;
  line-height: 12px;
  color: #6d7a87;
  opacity: 0;
  transition: opacity 0.15s ease-in-out;
}

.payment-methods {
  width: calc(100% - 26px);
  margin: 0 auto 30px;
  background-color: #f4f5f6;
  padding: 12px 0;
  height: 120px;
  max-width: 664px;
}

.payment-methods__title {
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 0.4px;
  margin-bottom: 12px;
  margin-left: 24px;
}

.payment-methods-list {
  overflow-y: scroll;
  overflow-x: hidden;
  -ms-overflow-style: none;
  transform: rotate(270deg) translateX(-100%);
  transform-origin: top left;
  position: absolute;
  width: 56px;
  height: calc(100vw - 26px);
}

.payment-methods-list::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.payment-methods-list::-webkit-scrollbar-button {
  width: 0;
  height: 0;
}

.payment-methods-list__inner {
  transform: rotate(90deg) translateY(-56px);
  transform-origin: top left;
  display: flex;
}

.payment-methods-list__inner:after {
  content: '';
  display: block;
  width: 26px;
  height: 56px;
  flex-shrink: 0;
}

.payment-method {
  display: inline-flex;
  background: #f9fafa;
  border: 1px solid #dde0e4;
  border-radius: 2px;
  width: 120px;
  height: 56px;
  align-items: center;
  justify-content: center;
  margin-left: 4px;
  flex-shrink: 0;
  transition: box-shadow 0.15s ease-in-out;
}

.payment-method:first-child {
  margin-left: 24px;
}

.news-list {
  padding: 0;
  list-style: none;
  margin: 0;
}

.news-list-item {
  position: relative;
  margin-bottom: 20px;
}

.news-list-item:not(.news-list-item_big) {
  padding: 0 20px 20px;
  border-bottom: 1px solid #ebeef2;
}

.news-list-item:last-child {
  margin-bottom: 0;
}

.news-list-item_big .news-list-item__descr {
  padding: 16px 20px 9px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(to top, #000 -6.25%, rgba(0, 0, 0, 0) 100%);
}

.news-list-item_big .news-list-item__link {
  color: #fff;
}

.news-list-item_big .news-list-item__photo {
  width: 100%;
  margin-right: 0;
}

.news-list-item_big .news-list-item__title {
  font-weight: bold;
  font-size: 16px;
  line-height: 14px;
}

.news-list-item_big .news-list-item__date {
  font-size: 12px;
  line-height: 14px;
  margin-bottom: 0;
}

.news-list-item__link {
  position: relative;
  display: flex;
  color: #2a2f34;
}

.news-list-item__photo {
  width: 148px;
  margin-right: 10px;
  font-size: 10px;
  flex-shrink: 0;
}

.news-list-item__img {
  display: block;
  width: 100%;
}

.news-list-item__title {
  font-weight: 500;
  font-size: 13px;
  line-height: 15px;
}

.news-list-item__date {
  font-size: 11px;
  line-height: 13px;
  margin-bottom: 10px;
}

@media (min-width: 751px) {
  .payment-methods-list {
    height: 720px;
  }
}

@media (min-width: 800px) {
  .main {
    box-shadow: 0 0 0 1px #dedede;
    margin-top: 67px;
    padding-bottom: 40px;
    margin-bottom: 40px;
  }

  .form-auth {
    margin-bottom: 32px;
    padding: 74px 0 68px;
  }

  .form-auth__wrapper {
    width: 100%;
    max-width: 720px;
    display: grid;
    grid-template-columns: 1fr 1fr 170px;
    grid-column-gap: 19px;
  }

  .form-row {
    margin-bottom: 0;
  }

  .payment-methods {
    max-width: 720px;
    height: 162px;
    margin-bottom: 58px;
  }

  .payment-methods__title {
    margin-left: 32px;
    margin-bottom: 26px;
  }

  .payment-methods-list {
    width: 88px;
  }

  .payment-methods-list__inner {
    transform: rotate(90deg) translateY(-88px);
  }

  .payment-methods-list__inner:after {
    width: 32px;
    height: 76px;
  }

  .payment-method {
    width: 156px;
    height: 76px;
    margin-left: 5px;
  }

  .payment-method:first-child {
    margin-left: 32px;
  }

  .payment-method:hover {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.11), 0 2px 2px rgba(0, 0, 0, 0.11), 0 4px 4px rgba(0, 0, 0, 0.11), 0 6px 8px rgba(0, 0, 0, 0.11);
  }

  .news-list {
    display: grid;
    grid-template-columns: 540px 240px;
    grid-template-rows: repeat(2, 130px);
    grid-gap: 10px;
  }

  .news-list-item {
    margin-bottom: 0;
  }

  .news-list-item:not(.news-list-item_big) {
    padding: 0;
    border-bottom: none;
    width: 240px;
    overflow: hidden;
  }

  .news-list-item_big {
    grid-row: span 2 / auto;
  }

  .news-list-item_big .news-list-item__descr {
    padding-bottom: 23px;
  }

  .news-list-item_big .news-list-item__date {
    margin-bottom: 4px;
  }

  .news-list-item__link {
    color: #fff;
    height: 100%;
  }

  .news-list-item__link:hover .news-list-item__title {
    text-decoration: underline;
  }

  .news-list-item__photo {
    width: 100%;
    margin-right: 0;
    height: 100%;
    object-fit: cover;
  }

  .news-list-item__descr {
    padding: 16px 10px 8px;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: linear-gradient(to top, #000 -6.25%, rgba(0, 0, 0, 0) 100%);
    transition: padding-top 0.15s ease-in-out;
  }

  .news-list-item__link:hover .news-list-item__descr {
    padding-top: 30px;
  }

  .news-list-item__date {
    margin-bottom: 0;
  }
}